import NewsCard from "./newsCard";

import styles from "./news.module.css"

import photo1 from "../../images/news/new-photo.png"
import photo2 from "../../images/news/news1.png"
import photo3 from "../../images/news/news2.png"
import arrow from "../../images/news/arrow.svg"

function News() {
    return (
        <div className={styles.wrapper}>
        <div className="text-regular medium mt-10 mb-6"></div>
        <div className={styles.news}>
            <NewsCard
                photo={photo1}
                day={"24"}
                mounth={""}
                title={"    "}
            />
            <NewsCard
                photo={photo2}
                day={"15"}
                mounth={""}
                title={"     , : "}
            />
            <NewsCard
                photo={photo3}
                day={"16"}
                mounth={""}
                title={"     "}
            />
            <NewsCard
                photo={photo1}
                day={"2"}
                mounth={""}
                title={"    "}
            />
            <NewsCard
                photo={photo2}
                day={"24"}
                mounth={""}
                title={"     "}
            />
            <NewsCard
                photo={photo3}
                day={"4"}
                mounth={""}
                title={"     , : "}
            />
        </div>
        <a className={`${styles.link} text-small medium mt-8 cl-b`} href="#">  <img src={arrow} alt="all news" /></a>
        </div>
    
    );
  }
  
  export default News;
  